<template>
  <v-data-table
      :headers="headers"
      :items="lectureArray"
      item-key="lectureId"
      :item-class="submitCheck"
      class="elevation-1"
      id="table-width-working-id"
      hide-default-footer=false	
    >
      <template v-slot:[`item.learningState`]="{ item }">
        <v-hover
          v-slot="{ hover }"
          open-delay="200"
        >
          <v-progress-linear
            v-if="item.status == '1'"
            color="light-blue"
            height="18"
            striped
            :value="StudyingPercentage(item.learningTime, item.fullTime)"
          >
            <strong v-if="!hover">{{ remainingTime(item.learningTime, item.fullTime, item.status) }}</strong>
            <strong v-else>{{ item.learningTime +'/'+ item.fullTime }}</strong>
          </v-progress-linear>
          <v-progress-linear
            v-else-if="item.learningTime == '0'"
            color="blue-grey"
            height="18"
            :value="StudyingPercentage(item.learningTime, item.fullTime)"
          >
            <strong v-if="!hover">{{ remainingTime(item.learningTime, item.fullTime, item.status) }}</strong>
            <strong v-else>{{ item.learningTime +'/'+ item.fullTime }}</strong>
          </v-progress-linear>
          <v-progress-linear
            v-else-if="item.type != ''"
            color="blue-grey"
            height="18"
            :value="StudyingPercentage(item.learningTime, item.fullTime)"
          >
            <strong>Empty</strong>
          </v-progress-linear>
          <v-progress-linear
            v-else
            color="amber"
            height="18"
            :value="StudyingPercentage(item.learningTime, item.fullTime)"
          >
            <strong v-if="!hover">{{ remainingTime(item.learningTime, item.fullTime, item.status) }}</strong>
            <strong v-else>{{ item.learningTime +'/'+ item.fullTime }}</strong>
          </v-progress-linear>
        </v-hover>
      </template>
      <template v-slot:[`item.status`]="{ item }" >
        <v-icon v-if="item.status == '1'"  color="green accent-3">mdi-check-bold</v-icon>
        <v-icon v-else color="red darken-1">mdi-video-off-outline</v-icon>
      </template>
      <!-- <template v-slot:[`item.lectureVideoId`]="{ item }">
          <v-btn
            color="primary"
            dark
            href="https://cyber.inhatc.ac.kr/"
            target="_blank"
          >
            {{item.status}}
          </v-btn>
      </template> -->
    </v-data-table>
</template>

<script>
export default {
  name: 'LectureDescription',
  data() {
    return {
      LectureTitle: ' ',
      expanded: [],
      singleExpand: true,
      headers: [
          { text: ' ', value: 'studyYN', width: '5%', class: "blue lighten-5" },//
          { text: ' ', value: 'idx', align: 'center', width: '5%', class: "blue lighten-5" },
          {
            text: '',
            align: 'center',
            sortable: false,
            value: 'title',
            width: '40%',
            class: "blue lighten-5",
            //primary
          },
          { text: '', value: 'learningState', width: '35%', class: "blue lighten-5"},
          // { text: '', value: 'lectureVideoId', width:'15%', class: "blue lighten-5" },
          
          // { text: '', value: 'description' },
      ],
    }
  },
  computed: {
    
  },
  props:{
    lectureArray: Object,
  },
  methods: {
    StudyingPercentage(learningTime, fullTime) {
      //    type   .
      const numFullTime = Number(fullTime.slice(0, fullTime.indexOf('')));
      if(numFullTime == 0) return 100;

      const numStudyingTime = Number(learningTime.slice(0, learningTime.indexOf('')));
      if(numStudyingTime > numFullTime) return 100;
      const percent = numStudyingTime / numFullTime * 100;
      return Math.ceil(percent);
    },
    remainingTime(learningTime, fullTime, statusLecture){
      const studyTime = learningTime.split(' ');
      const fullTimeMinute = (fullTime.indexOf('') == -1) ? 0 : Number(fullTime.replace('', ''));
      const studyTimeMinute = (studyTime[0].indexOf('') == -1) ? 0 : Number(studyTime[0].replace('', ''));

      const fullTimeSecond = fullTimeMinute * 60;
      const studyTimeSecond = ((studyTime[0].indexOf('') == -1) ? 0 : Number(studyTime[1].replace('', ''))) + studyTimeMinute * 60;
      if(studyTimeSecond >= fullTimeSecond) {
        if(statusLecture == '1' ) return ' ';
        else return '  ';
      };

      let remainingTimeSecond = fullTimeSecond - studyTimeSecond;
      if(remainingTimeSecond < 60) return remainingTimeSecond+'';
      
      const remainingTimeMinute = Math.floor(remainingTimeSecond / 60);
      remainingTimeSecond -= remainingTimeMinute * 60;
      return remainingTimeMinute+' ' + remainingTimeSecond +''
    }
  },
}
</script>

<style scoped>
  
</style>



