<template>
    <v-card class="elevation-12 ma-8">
        <v-toolbar dark color="primary">
            <v-toolbar-title>
                <v-btn
                    color="white"
                    icon
                    @click="clickLogin()"
                >
                    <v-icon>mdi-arrow-u-right-top-bold</v-icon>
                </v-btn>
                Esummary 
            </v-toolbar-title>
        </v-toolbar>
        <v-progress-linear color="deep-purple" height="10" :indeterminate="loading" :active="loading"></v-progress-linear>
        <v-form ref="form" v-model="valid" lazy-validation class="ma-8">
            <v-text-field v-model="studentId" :rules="studentIdRules" label=" ID" required></v-text-field>
        
            <v-text-field v-model="password" :rules="passwordRules" label=" Password" type="password" required></v-text-field>

            <v-text-field v-model="nickname" :rules="nicknameRules" label="" required></v-text-field>
        
            <v-checkbox v-model="checkbox" :rules="[v => !!v || ' !']" label=" " required>
            </v-checkbox>
        
            <v-btn :disabled="!valid" color="success" class="mr-4 mb-4" @click="validate">
                
            </v-btn>
        
            <v-btn color="error" class="mr-4 mb-4" @click="reset">
                Form 
            </v-btn>
            <!-- 
            <v-btn color="warning" @click="resetValidation">
                Reset Validation
            </v-btn> 
            -->
        
        </v-form>
    </v-card>
</template>

<script>
import {SET_LOGIN_FLAG} from '@/store/store'
import { mapState } from 'vuex';
import store from '@/store/store';
import * as authApi from '@/api/auth';

// import { Popup_showSignUpSuccess } from '../../../store/store';

export default {
    name: 'SignUpCard',
    components: {
        // LoginSuccessPopup
    },
    data() {
        return {
            loading: false,

            valid: true,
            studentId: '',
            studentIdRules: [
                v => !!v || 'ID  .',
                v => (v && v.length >= 5) || 'ID 5  ',
            ],
            password: '',
            passwordRules: [
                v => !!v || 'Password  .',
                v => (v && v.length >= 5) || 'Password 5  ',
                //
            ],
            nickname: '',
            nicknameRules: [
                v => !!v || 'Nickname  .',
                v => (v && v.length >= 3 && v.length <= 10) || 'Nickname 3  10  ',
            ],
            checkbox: false,
        };
    },
    computed: {
    },
    methods: {
        clickLogin() {
            this.$store.commit(SET_LOGIN_FLAG, true); // 
        },
        async validate() {
            this.$refs.form.validate()
            this.loading = true;

            // 
            const response = await authApi.signup(this.studentId, this.password, this.nickname);

            if(response === undefined) {
                //    
                this.loading = false;
                console.log(' ');
                this.resetValidation();
                // this.$store.commit(Popup_showSignUpSuccess, {text: ' .'});
                return;
            }
            
            //  ?   
            console.log(' ');
            this.$store.commit(SET_LOGIN_FLAG, true); //   
            // this.$store.commit(Popup_showSignUpSuccess, {text: ' .'});
            this.loading = false;
        },
        reset() {
            //  
            this.$refs.form.reset()
        },
        resetValidation() {
            //   
            this.$refs.form.resetValidation()
        },
    },
};
</script>


