<template>
<!--
   !
   min-height          .   .
-->
  <v-card
    class="mx-auto"
    id="main-card"
  >
    <v-card-text>
      <p class="body-1"> </p>
      <div class="text-justify">
        {{showDescription()}}
      </div>
    </v-card-text>
    <!-- <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          dark
          href="https://cyber.inhatc.ac.kr/"
          target="_blank"
        >
              
      </v-btn>
    </v-card-actions> -->
  </v-card>
</template>

<script>

export default {
  name: 'DescriptionCard',
  data() {
    return {
      reveal: false,
    }
  },
  props:{
    description: String,
  },
  methods: {
    showDescription() {
      //  computed .
      if(this.description == null || this.description == '') {
        return "     !<br>      .";
      }
      else {
        return this.description;
      }
    }
  },
  computed: {
    
  }, 
}
</script>

<style scoped>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
#main-card{
  margin: 0;
  border-radius: 0;
}
</style>



